* {
  box-sizing: border-box;
}

body {
  background: #222;
  margin: 0;
}

div {
  height: 18.875em;
  width: 34em;
  background: linear-gradient(to bottom right, #0000004d, #0000), url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/187/vwood.png");
  border: 1px solid #160801;
  border-radius: 1em;
  margin: 5em auto;
  padding: 3em 0 0 3em;
  position: relative;
  box-shadow: inset 0 0 50px #00000080, inset 0 1px #d4987d33, 0 5px 15px #00000080;
}

button {
  float: left;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}

div .white {
  height: 16em;
  width: 4em;
  z-index: 1;
  background: linear-gradient(#eee 0%, #fff 100%);
  border-bottom: 1px solid #bbb;
  border-left: 1px solid #bbb;
  border-radius: 0 0 5px 5px;
  box-shadow: inset -1px 0 #fffc, inset 0 0 5px #ccc, 0 0 3px #0003;
}

div .white:active {
  background: linear-gradient(#fff 0%, #e9e9e9 100%);
  border-top: 1px solid #777;
  border-bottom: 1px solid #999;
  border-left: 1px solid #999;
  box-shadow: inset 2px 0 3px #0000001a, inset -5px 5px 20px #0003, 0 0 3px #0003;
}

.black {
  height: 8em;
  width: 2em;
  z-index: 2;
  background: linear-gradient(45deg, #222 0%, #555 100%);
  border: 1px solid #000;
  border-radius: 0 0 3px 3px;
  margin: 0 0 0 -1em;
  box-shadow: inset -1px -1px 2px #fff3, inset 0 -5px 2px 3px #0009, 0 2px 4px #00000080;
}

.black:active {
  background: linear-gradient(to right, #444 0%, #222 100%);
  box-shadow: inset -1px -1px 2px #fff3, inset 0 -2px 2px 3px #0009, 0 1px 2px #00000080;
}

.a, .g, .f, .c, .d {
  margin: 0 0 0 -1em;
}

.e, .b {
  margin: 0 1em 0 -1em;
}

div button:first-child {
  border-radius: 5px 0 5px 5px;
}

div button:last-child {
  border-radius: 0 5px 5px;
}

